import React from 'react'
import styles from './CallsHeader.module.css';

export default function CallsHeader(props:any) {
  return (
    <div className={styles.callsHeader}>
      <div className={styles.arrow}></div>
      <div className={styles.time_of_call}></div>
      <div className={styles.person_avatar}>C</div>
      <div className={styles.from_site}></div>
      <div className={styles.client_info}></div>
      <div className={styles.source}></div>
      <div className={styles.score}></div>
      <div className={styles.time}></div>
    </div>
  )
}
