import styles from './Sidebar.module.css';
import Logo from '../../icons/Logo.svg';
import ResultsIcon from '../../icons/ResultsIcon.svg';
import OrdersIcon from '../../icons/OrdersIcon.svg';
import MailIcon from '../../icons/MailIcon.svg';
import CallsIcon from '../../icons/CallsIcon.svg';
import ClientsIcon from '../../icons/ClientsIcon.svg';
import DocsIcon from '../../icons/DocsIcon.svg';
import EmployeeIcon from '../../icons/EmployeeIcon.svg';
import ReportIcon from '../../icons/ReportIcon.svg';
import InfoIcon from '../../icons/InfoIcon.svg';
import SettingsIcon from '../../icons/SettingsIcon.svg';




import SidebarMenuItem from '../SidebarMenuItem/SidebarMenuItem';

export default function Sidebar() {
  return (
    <div className={styles.sidebar}>
      <img src={Logo} className={styles.logo}></img>
      <SidebarMenuItem icon={ResultsIcon} name={''}/>
      <SidebarMenuItem icon={OrdersIcon} name={''}/>
      <SidebarMenuItem icon={MailIcon} name={''}/>
      <SidebarMenuItem icon={CallsIcon} name={''}/>
      <SidebarMenuItem icon={ClientsIcon} name={''}/>
      <SidebarMenuItem icon={DocsIcon} name={''}/>
      <SidebarMenuItem icon={EmployeeIcon} name={''}/>
      <SidebarMenuItem icon={ReportIcon} name={''}/>
      <SidebarMenuItem icon={InfoIcon} name={' '}/>
      <SidebarMenuItem icon={SettingsIcon} name={''}/>
    </div>
  )
}

